import { useState } from "react";
import { user } from "../../../../data/mockData";
import { GameModeCard } from "@/components/play/GameModeCard";
import { AISphereIllustration, PingPongTableIllustration, TournamentSceneIllustration } from "@/components/play/Illustration";

// Invite Modal Component
export const InviteModal = ({ isOpen, onClose }) => {
  const [searchQuery, setSearchQuery] = useState('');
  const [searchResults, setSearchResults] = useState([]);

  // Mock search function - replace with actual API call
  const handleSearch = (query) => {
    if (query.trim()) {
      // Mock search results
      const mockResults = [
        { id: 1, username: 'player1', nickname: 'Pro Player', avatar: '/mghalmi.png', online: true },
        { id: 2, username: 'gamer2', nickname: 'Chess Master', avatar: '/mghalmi.png', online: false },
        { id: 3, username: 'user3', nickname: 'Ping Pong Pro', avatar: '/mghalmi.png', online: true },
      ].filter(user => 
        user.username.toLowerCase().includes(query.toLowerCase()) ||
        user.nickname.toLowerCase().includes(query.toLowerCase())
      );
      setSearchResults(mockResults);
    } else {
      setSearchResults([]);
    }
  };

  const handleInvite = (user) => {
    console.log('Inviting user:', user);
    // Here you would send the actual invite
    alert(`Invite sent to ${user.nickname}!`);
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 backdrop-blur-xs flex items-center justify-center z-50">
      <div className="bg-[#121417] rounded-lg p-8 w-full max-w-2xl mx-4 border border-gray-700 max-h-[80vh] overflow-hidden">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-2xl font-bold text-white">Invite Player</h2>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-white transition-colors text-xl"
          >
            ✕
          </button>
        </div>
        
        <div className="mb-6">
          <div className="relative">
            <input
              type="text"
              placeholder="Search for players..."
              value={searchQuery}
              onChange={(e) => {
                setSearchQuery(e.target.value);
                handleSearch(e.target.value);
              }}
              className="w-full px-4 py-3 bg-[#4a5568] text-white rounded-lg border-none outline-none focus:bg-[#5a6578] transition-colors pr-10"
            />
            <div className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400">
              🔍
            </div>
          </div>
        </div>

        <div className="overflow-y-auto max-h-96">
          {searchResults.length > 0 ? (
            <div className="space-y-3">
              {searchResults.map((user) => (
                <div key={user.id} className="flex items-center justify-between p-4 bg-[#252a32] rounded-lg hover:bg-[#2d3748] transition-colors">
                  <div className="flex items-center space-x-4">
                    <img
                      src={user.avatar}
                      alt={user.nickname}
                      className="w-12 h-12 rounded-full object-cover"
                    />
                    <div>
                      <h3 className="text-white font-semibold">{user.nickname}</h3>
                      <p className="text-gray-400 text-sm">@{user.username}</p>
                    </div>
                    <div className={`w-3 h-3 rounded-full ${user.online ? 'bg-green-500' : 'bg-gray-500'}`}></div>
                  </div>
                  <button
                    onClick={() => handleInvite(user)}
                    disabled={!user.online}
                    className={`px-4 py-2 rounded-lg transition-colors ${
                      user.online
                        ? 'bg-blue-600 hover:bg-blue-700 text-white'
                        : 'bg-gray-600 text-gray-400 cursor-not-allowed'
                    }`}
                  >
                    {user.online ? 'Invite' : 'Offline'}
                  </button>
                </div>
              ))}
            </div>
          ) : searchQuery ? (
            <div className="text-center py-8 text-gray-400">
              No players found matching "{searchQuery}"
            </div>
          ) : (
            <div className="text-center py-8 text-gray-400">
              Start typing to search for players
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
