"use client"
import Image from 'next/image';
import React, { useState } from 'react';
import { TabNavigation } from '../play';
import { Local1v1 } from './OneVsOneLocale';
import { InviteModal } from './OneVsOneOnline';

// Main Component
export default function ChooseYourGame() {
  const [selectedTab, setSelectedTab] = useState('Local');
  const [currentView, setCurrentView] = useState('choose'); // 'choose' or 'onevsone'
  const [showInviteModal, setShowInviteModal] = useState(false);

  const handleNavigateToOneVsOne = () => {
    setCurrentView('onevsone');
  };

  const handleBackToChoose = () => {
    setCurrentView('choose');
  };

  // If we're in the OneVsOne view, render the Local1v1 component
  if (currentView === 'onevsone') {
    return <Local1v1 onBack={handleBackToChoose} />;
  }

  return (
    <div className="h-full text-white">
      <main className="pt-20 p-3 sm:p-4 lg:p-6 w-full min-h-screen">
        <div className="max-w-7xl mx-auto">
          <h1 className="text-3xl sm:text-4xl font-bold text-center mb-8">
            Choose Your Game
          </h1>

          <TabNavigation 
            selectedTab={selectedTab} 
            onTabChange={setSelectedTab} 
          />

          {selectedTab === 'Local' ? (
            <LocalGames onNavigateToOneVsOne={handleNavigateToOneVsOne} />
          ) : (
            <OnlineGames onShowInviteModal={() => setShowInviteModal(true)} />
          )}
        </div>
      </main>

      {/* Invite Modal */}
      <InviteModal
        isOpen={showInviteModal}
        onClose={() => setShowInviteModal(false)}
      />
    </div>
  );
}
